.btn_wrap{display:flex;justify-content:space-between;width:800px;margin:30px auto}.btn_wrap .modal_btn{background-color:green;border:1px solid green;color:#fff;cursor:pointer;padding:10px;width:20%}.btn_wrap .modal_btn:hover{background-color:#fff;color:green}.modal_wrap{display:none;position:relative}.modal_wrap .modal_bg{background-color:rgba(0,0,0,0.7);height:100%;left:0;position:fixed;top:0;width:100%;z-index:1}.modal_wrap .modal_contents{background-color:#fff;position:absolute;left:50%;top:50%;transform:translate(-50%, -50%);margin:auto;padding:30px;max-width:400px;z-index:2}.modal_wrap .modal_text{font-size:20px;margin:10px 0}.modal_wrap .modal_close{background-color:green;border:1px solid green;color:#fff;cursor:pointer;display:block;padding:10px;width:100%}.modal_wrap .modal_close:hover{background-color:#fff;color:green}.modal_wrap.open{display:block}#modal1{color:red}
